body {
  background-attachment: fixed;
  background-repeat: repeat;
  font-size: 100%;
  /* equivalent to 16px - must have for Safari */
  font-weight: 300;
  font-family: "Muli";
  line-height: 1.7;
  margin: 0;
  min-height: 100vh;
  padding: 0;
  position: relative;
  width: 100%;
  z-index: 1; }
  body:after {
    background: -moz-linear-gradient(-45deg, #f1f1f1 0%, rgba(241, 241, 241, 0) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, #f1f1f1 0%, rgba(241, 241, 241, 0) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, #f1f1f1 0%, rgba(241, 241, 241, 0) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f1f1f1', endColorstr='#00f1f1f1',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */
    content: '';
    display: block;
    height: 100%;
    opacity: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1; }
  body:before {
    background: -moz-linear-gradient(top, #f1f1f1 0%, rgba(241, 241, 241, 0) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #f1f1f1 0%, rgba(241, 241, 241, 0) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #f1f1f1 0%, rgba(241, 241, 241, 0) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f1f1f1', endColorstr='#00f1f1f1',GradientType=0 );
    /* IE6-9 */
    content: '';
    display: block;
    height: 100%;
    opacity: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 0; }
